import React from 'react';
import { history } from "routes/history";
import { BsArrowLeft } from "react-icons/bs";

import "./styles.css";

const PrivacyPolicy = () => {
  return (
    <div id="privacyPolicy" className="privacy-policy-container">
      <header
        className="arrow-back"
        onClick={() => {
          history.goBack();
        }}
      >
        <BsArrowLeft className="arrow-back" />
        <strong>Voltar</strong>
      </header>

      <section className="title">
        <h1><b>
          Poltica de Privacidade
        </b></h1>
      </section>

      <section>
        <h3><b>
          1. OBTENO E USO DE INFORMAES PESSOAIS
        </b></h3>

        <p>
          Esta poltica se aplica a qualquer usurio dos servios da Delivery Web e a qualquer pessoa que entre em contato com a Delivery Web ou que envie informaes para a Delivery Web. Os dados sero obtidos quando o USURIO:
        </p>
        <p>
           Realizar o cadastro em nosso aplicativo ou site
        </p>
        <p>
           Solicitar entregas de pedidos (USURIOS)
        </p>
        <p>
           Prestar servios de entrega (ENTREGADORES)
        </p>
        <p>
           Entrar em contato por meio dos canais de comunicao disponveis na plataforma
        </p>
      </section>

      <section>
        <h3><b>
          2. QUAIS INFORMAES OBTEMOS
        </b></h3>

        <h5><b>
          INFORMAES QUE VOC NOS FORNECE
        </b></h5>

        <p>
           Cria ou atualiza sua conta na Delivery Web como: seu nome, nmero de telefone, CPF, endereo, informaes para pagamento.
        </p>
        <p>
           Solicita a entrega de produtos atravs do app da Delivery Web.
        </p>
        <p>
           Entra em contato com a Delivery Web, inclusive para suporte ao cliente.
        </p>
        <p>
           Habilita recursos que requerem o acesso da Delivery Web ao seu catlogo de contatos.
        </p>

        <h5><b>
          DADOS DE LOCALIZAO
        </b></h5>

        <p>
           A Delivery Web pode coletar seus dados de localizao exatos ou aproximados conforme determinado atravs de dados como GPS, endereo IP e WiFi.
        </p>
        <p>
           Se voc  um ENTREGADOR, a Delivery Web coleta dados de localizao quando o aplicativo est sendo executado em primeiro plano ou segundo plano no seu aparelho. Essas informaes so utilizadas para informar ao USURIO a localizao de seu pedido em tempo real.
        </p>
        <p>
           Se voc for um USURIO, a Delivery Web pode coletar informaes de localizao quando o aplicativo Delivery Web estiver funcionando em primeiro plano.  possvel utilizar a Delivery Web sem permitir que a Delivery Web colete seus dados de localizao. No entanto, isso pode afetar as funcionalidades disponveis no seu aplicativo, por exemplo, se voc no permitir que a Delivery Web colete seus dados de localizao, voc ter que inserir manualmente seu endereo para realizar pedidos atravs da plataforma.
        </p>


      </section>

      <section>
        <h3><b>
          3. USO DOS DADOS E INFORMAES
        </b></h3>

        <h5><b>
          A Delivery Web USA AS INFORMAES COLETADAS PARA:
        </b></h5>

        <p>
          A Delivery Web usa as informaes coletadas para fornecer, personalizar, manter e melhorar nossos produtos e servios. Isso inclui o uso das informaes para:
        </p>
        <p>
           Possibilitar que entregas sejam realizadas
        </p>
        <p>
           Processar ou facilitar pagamentos por esses servios
        </p>
        <p>
           Habilitar recursos que permitem que voc compartilhe informaes com outras pessoas, como quando indica um amigo para usar a Delivery Web
        </p>
        <p>
           Habilitar recursos para personalizar sua conta Delivery Web, como criar marcadores para seus estabelecimentos favoritos
        </p>
        <p>
           Executar operaes necessrias para prestar nossos servios, como para solucionar problemas de operao, conduzir anlises de dados, testes e pesquisas e monitorar e analisar a utilizao e as tendncias da atividade
        </p>

      </section>

      <section>
        <h3><b>
          4. COOKIES E OUTRAS TECNOLOGIAS
        </b></h3>

        <p>
          O site da Delivery Web, os aplicativos, as mensagens de e-mail e o material publicitrio podero usar "cookies" e outras tecnologias tais como pixel tags e web beacons. Essas tecnologias nos ajudam a entender melhor o comportamento do usurio, nos dizem quais partes de nosso site as pessoas visitaram e facilitam e medem a eficcia da publicidade e das pesquisas na web
        </p>
        <p>
          A Delivery Web tambm usa cookies e outras tecnologias para relembrar informaes pessoais quando voc usa o aplicativo. O nosso objetivo nesses casos  fazer com que a sua experincia no aplicativo seja mais conveniente e personalizada. Por exemplo, saber que algum usando o seu computador ou dispositivo adquiriu determinado produto nos permite indicar produtos mais relevantes para voc no prximo acesso ao aplicativo.
        </p>
        <p>
          Em algumas de nossas mensagens de e-mail, ns usamos uma "URL click-through" vinculada ao contedo do site da Delivery Web. Quando os clientes clicam em uma dessas URLs, os usurios so enviados para um servidor web diferente antes de chegarem  pgina de destino no nosso site. Ns monitoramos esses dados de click-through para entender o interesse em determinados tpicos e avaliar a eficcia das comunicaes com os nossos clientes. Se voc preferir no ser monitorado dessa maneira, no clique em texto nem nos links de imagens nas mensagens de e-mail.
        </p>

      </section>

      <section>
        <h3><b>
          PROTEO DE INFORMAES PESSOAIS
        </b></h3>

        <p>
          O Delivery Web toma precaues, entre elas medidas administrativas, tcnicas e fsicas, para proteger as suas informaes pessoais contra perda, roubo, uso indevido, bem como contra acesso no autorizado, divulgao, alterao e destruio.
        </p>
      </section>

      <section>
        <h3><b>
          INTEGRIDADE E RETENO DE INFORMAES PESSOAIS
        </b></h3>

        <p>
          A Delivery Web reter suas informaes enquanto sua conta permanecer ativa. A Delivery Web ir excluir ou anonimizar suas informaes mediante solicitao exceto se houver uma questo no resolvida relacionada  sua conta, como um crdito pendente em sua conta ou uma reclamao ou disputa no resolvida;
        </p>
      </section>

      <section>
        <h3><b>
          DVIDAS SOBRE PRIVACIDADE
        </b></h3>

        <p>
          Se voc tiver algum questionamento ou dvida com relao  Poltica de Privacidade da Delivery Web ou qualquer prtica descrita aqui, entre em contato conosco atravs do email contato@Delivery Web.delivery
        </p>

        <p>
          A Delivery Web poder atualizar a sua Poltica de Privacidade periodicamente. Se fizermos alguma alterao na poltica em termos materiais, colocaremos um aviso no nosso site, juntamente com a Poltica de Privacidade atualizada
        </p>
      </section>

    </div>
  );
};

export default PrivacyPolicy;